"use client";

import { useEffect, useState } from "react";

interface Prediction {
  id: number;
  date: string;
  region: string;
  risk_score: number;
  sentiment: string;
  headline?: string;
}

export default function HomePage() {
  const [predictions, setPredictions] = useState<Prediction[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function load() {
      try {
        const res = await fetch("/api/predictions");
        const data = await res.json();
        if (data.success) setPredictions(data.data || []);
      } catch (err) {
        console.error("‚ùå Failed to fetch predictions:", err);
      } finally {
        setLoading(false);
      }
    }
    load();
  }, []);

  const getColor = (score: number) => {
    if (score >= 70) return "text-red-600";
    if (score >= 50) return "text-amber-500";
    return "text-blue-600";
  };

  return (
    <main className="min-h-screen bg-slate-50 p-8">
      <div className="max-w-6xl mx-auto space-y-8">
        {/* === HEADER === */}
        <header>
          <h1 className="text-2xl font-bold text-slate-900">
            eX Intelligence Dashboard
          </h1>
          <p className="text-sm text-slate-500">
            Organize everything. Predict anything.
          </p>
        </header>

        {/* === DASHBOARD GRID === */}
        <div className="grid md:grid-cols-3 gap-6">
          {/* Crash Watch placeholder */}
          <div className="p-4 bg-white rounded-xl border">
            <h2 className="font-semibold text-slate-800 mb-1">üö® Crash Watch</h2>
            <p className="text-sm text-slate-500">
              Tracks risk levels and upcoming financial turbulence by region.
            </p>
          </div>

          {/* Global Overview placeholder */}
          <div className="p-4 bg-white rounded-xl border">
            <h2 className="font-semibold text-slate-800 mb-1">üåê Global Overview</h2>
            <p className="text-sm text-slate-500">
              Visualization placeholder (donut / globe integration)
            </p>
            <div className="h-32 grid place-items-center text-xs text-slate-400">
              [ Interactive Visualization ]
            </div>
          </div>

          {/* === Predictive Timeline === */}
          <div className="p-4 bg-white rounded-xl border">
            <h2 className="font-semibold text-slate-800 mb-3">üìà Predictive Timeline</h2>
            {loading ? (
              <div className="text-sm text-slate-500">Loading predictions‚Ä¶</div>
            ) : predictions.length === 0 ? (
              <div className="text-sm text-slate-500">
                No predictions found. Add data in Supabase.
              </div>
            ) : (
              <div className="space-y-3 max-h-[300px] overflow-y-auto pr-1">
                {predictions.map((p) => (
                  <div
                    key={p.id}
                    className="flex justify-between items-center border rounded-lg p-2 hover:bg-slate-50 transition"
                  >
                    <div>
                      <div className="text-sm font-medium text-slate-800">
                        {p.headline || p.region}
                      </div>
                      <div className="text-xs text-slate-500">
                        {p.date} ‚Ä¢ {p.region} ‚Ä¢ {p.sentiment}
                      </div>
                    </div>
                    <div className={`text-sm font-semibold ${getColor(p.risk_score)}`}>
                      {p.risk_score}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </main>
  );
}
